* {
  margin: 0;
  padding: 0;

  font-family: "Roboto", sans-serif;
}
a{
  text-decoration: none;
  color: var(--green-light);
}


body {
  background: #94C3CC;
}


:root {
  /* colors */
  --black: #3B434E;
  --white: white;
  --primary: #7653FC;
  --primary-hover: #5075D5;
  --red: #FC836D;
  --green: #1DC37E;
  --green-light: #C8F8E4;
  --font-family: "Noto Sans Display", sans-serif;
  --gray-light: #7A7777;
  --aqua: #bde3da;
  --argentum: linear-gradient(121.57deg, #FFF 18.77%, rgb(255 255 255 / 66%) 60.15%);

  /* ui */
  --transition: all 0.5s ease-in-out;
}

